  display: none;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 85%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

.sombra {
  -webkit-box-shadow: 0px -1px 5px 7px rgba(158, 158, 158, 0.33);
  -moz-box-shadow: 0px -1px 5px 7px rgba(158, 158, 158, 0.33);
  box-shadow: 0px -1px 5px 7px rgba(158, 158, 158, 0.33);
  background-color: var(--blanco);
  padding: 2rem;
  border-radius: 2rem;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.inicio {
  background-image: url(../../../build/img/PORTADA.png);
  height: 25rem;
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .inicio {
    height: 80rem;
  }
}
.inicio h1 {
  color: #fff;
  font-weight: bold;
}
.inicio h2 {
  color: #fff;
}

* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

.cont-temporizador {
  display: flex;
  justify-content: center;
  margin-top: 50px;
}

.cont-temporizador .bloque {
  color: #fff;
  margin: 0px 4px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.cont-temporizador .bloque p {
  color: #000000;
}
.cont-temporizador .bloque div {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.2);
  box-shadow: 0px 0px 6px 2px #727272 inset;
  color: #000000;
  font-size: 40px;
  font-weight: bold;
  width: 80px;
  height: 50px;
  margin-bottom: 10px;
  border-radius: 5px;
}

.fondo {
  background-image: url(../../../build/img/FONDO01.webp);
  height: auto;
  background-position: center center;
  background-size: cover;
  margin: 0;
}
.fondo img {
  height: 10rem;
  width: 15rem;
  display: flex;
  margin: auto;
}
@media (min-width: 768px) {
  .fondo img {
    height: 20rem;
    width: 30rem;
  }
}
.fondo h2 {
  font-family: "Playball", cursive;
  font-size: 2.8rem;
}
@media (min-width: 768px) {
  .fondo h2 {
    font-size: 3.4rem;
  }
}
.fondo h3 {
  padding: 2rem;
  font-size: 2.8rem;
}
@media (min-width: 768px) {
  .fondo h3 {
    font-size: 3.2rem;
  }
}
.fondo .cursiva {
  font-family: "Playball", cursive;
}

.seccion {
  margin: 0;
}
.seccion h3 {
  font-size: 2;
}
@media (min-width: 768px) {
  .seccion {
    display: grid;
    grid-template-columns: 50% 50%;
    column-gap: 1rem;
  }
  .seccion img {
    width: 15rem;
    height: 10rem;
  }
}
.seccion .foto img {
  width: 35rem;
  height: 30rem;
  margin-bottom: 2.5rem;
}
.seccion .boton {
  background-color: #E08709;
  color: #FFFFFF;
  display: flex;
  margin: auto;
  padding: 1rem 3rem 1rem 3rem;
  font-size: 2rem;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: bold;
  border-radius: 0.5rem;
  width: 70%;
  text-align: center;
  border: none;
  align-items: center;
}
.seccion .boton:hover {
  cursor: pointer;
  background-color: #000000;
}

.fondo2 {
  background-image: url(../../../build/img/FONDO02.png);
  height: auto;
  background-position: center center;
  background-size: cover;
}
.fondo2 h3 {
  margin-top: 0;
}
@media (min-width: 768px) {
  .fondo2 .seccion2 {
    display: grid;
    grid-template-columns: 50% 50%;
    column-gap: 6rem;
  }
}
.fondo2 h2 {
  font-family: "Playball", cursive;
  color: #0f73d7;
  font-size: 7rem;
}

.boton {
  background-color: #0f73d7;
  color: #FFFFFF;
  display: flex;
  margin: auto;
  margin-top: 2rem;
  padding: 1rem 3rem 1rem 3rem;
  font-size: 2rem;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: bold;
  border-radius: 0.5rem;
  width: 70%;
  text-align: center;
  border: none;
  align-items: center;
}

.boton:hover {
  cursor: pointer;
  background-color: #000000;
}

.fondo3 {
  background-color: #0f73d7;
}
.fondo3 h1 {
  margin-top: 0;
  color: #fff;
  font-family: "Playball", cursive;
  font-size: 4.5rem;
}
.fondo3 h3 {
  color: #fff;
}

@media (min-width: 768px) {
  .seccion3 {
    display: grid;
    grid-template-columns: 50% 50%;
    column-gap: 6rem;
  }
}

.boton {
  background-color: #fff;
  color: #0f73d7;
  display: flex;
  margin: auto;
  margin-top: 2rem;
  padding: 1rem 3rem 1rem 3rem;
  font-size: 2rem;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: bold;
  border-radius: 0.5rem;
  width: 70%;
  text-align: center;
  border: none;
  align-items: center;
}

.boton:hover {
  cursor: pointer;
  background-color: #000000;
}